import React from 'react';
import DepositStatusComponent from './DepositConfirmationComponent';
import PropTypes from 'prop-types';
import Modal from '../common/Modal/Modal';
import { getQueryParam } from '../../helpers/queryParamsHelper';

class DepositStatusModalComponent extends React.Component {
  constructor() {
    super();

    this.state = {
      txnState: '',
      title: ''
    };
  }

  componentDidMount() {
    const txnState = getQueryParam('txnState');
    const trustPayRedirect =
      getQueryParam('id') &&
      getQueryParam('resourcePath') &&
      getQueryParam('resourcePath').includes('payment');
    let title;

    if (trustPayRedirect || txnState === '1') {
      title = 'Deposit pending';
      this.setState(() => ({ txnState: 1, title }));
      this.props.openModal();
    }
    if (txnState === '2') {
      title = 'Deposit succesfull';
      this.setState(() => ({ txnState, title }));
      this.props.openModal();
    }
    if (txnState === '3') {
      title = 'Deposit error';
      this.setState(() => ({ txnState, title }));
      this.props.openModal();
    }
  }

  render() {
    const { close, isOpen } = this.props;

    const { txnState, title } = this.state;
    if (txnState) {
      return (
        <Modal
          isOpen={isOpen}
          title={this.context.t(title)}
          close={() => close()}>
          <DepositStatusComponent txnState={txnState} />
        </Modal>
      );
    } else {
      return null;
    }
  }
}

DepositStatusModalComponent.propTypes = {
  isOpen: PropTypes.bool,
  close: PropTypes.func.isRequired,
  openModal: PropTypes.func.isRequired
};

DepositStatusModalComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default DepositStatusModalComponent;



