import React from 'react';
import PropTypes from 'prop-types';

const DepositStatusComponent = (props, context) => {
  const { txnState } = props;
  const status = {
    '1': {
      status: 'pending',
      firstLine: context.t('Your deposit is being processed.'),
      secondLine: context.t('Your account balance will be updated soon.')
    },
    '2': {
      status: 'successful',
      firstLine: context.t('Your deposit was successfull.'),
      secondLine: context.t('Your account balance will be updated soon.')
    },
    '3': {
      status: 'failed',
      firstLine: context.t('There was a problem with your deposit.'),
      secondLine: context.t('Please try again or contact an administrator.')
    }
  };

  return (
    <div>
      <div>{status[txnState].firstLine}</div>
      <div>{status[txnState].secondLine}</div>
    </div>
  );
};

DepositStatusComponent.propTypes = {
  txnState: PropTypes.string.isRequired
};

DepositStatusComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default DepositStatusComponent;



