import { connect } from 'react-redux';
import DepositStatusModalComponent from './DepositConfirmationModalComponent';
import modalDecorator from '../../hocs/modalDecorator';
import { MODAL_TYPES, openModal } from '../../redux/actions/modalActions';

const mapDispatchToProps = dispatch => ({
  openModal: () => dispatch(openModal(MODAL_TYPES.DEPOSIT_STATUS))
});

const container = connect(
  null,
  mapDispatchToProps
)(DepositStatusModalComponent);

export default modalDecorator({
  name: MODAL_TYPES.DEPOSIT_STATUS
})(container);



